<template>
  <div class="about">
    <el-row>
      <el-col :span="8">
        <img class="center-block" :src="targetBook.imagePath" height="360"/>
      </el-col>
      <el-col :span="16">
        <router-link :to="'/abstract/' + targetBook.title">{{ targetBook.title }}</router-link>
        <p class="meta">{{ targetBook.author }}</p>
        <p class="lead">{{ targetBook.mabstract }}</p>
        <el-button type="primary">
          <router-link :to="'/preview/' + targetBook.title">阅读</router-link>
        </el-button>
        <el-button @click="addCollection">
          <span v-if="!isInCollection">加入收藏</span>
          <span v-else>取消收藏</span>
        </el-button>
      </el-col>
    </el-row>
    <div class="panel panel-default">
      <div class="panel-heading">
        <h3 class="panel-title">章节列表</h3>
      </div>
      <div class="panel-body">
        {{targetBook.phases}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  mounted () {
    this.getBookInfo()
  },
  data () {
    return {
      isInCollection: false,
      targetBook: {
        title: '',
        author: '',
        mAbstract: '',
        imagePath: ''
      }
    }
  },
  methods: {
    getBookInfo () {
      this.$http.get('/books/' + this.$route.params.title).then(res => {
        this.targetBook = res.data.data
      })
      const token = this.$keycloak.token
      const username = window.sessionStorage.getItem('username')
      if (!token) {
        this.isInCollection = false
        return
      }
      this.$http.get('/collect/' + username + '/collections/' + this.$route.params.title).then(res => {
        console.log(res.data)
        this.isInCollection = res.data.data
      })
    },
    addCollection () {
      const token = this.$keycloak.token
      const username = window.sessionStorage.getItem('username')
      if (!token) {
        this.$keycloak.login()
        return
      }
      if (this.isInCollection) {
        this.$http.delete('/collect/' + username + '/collections/' + this.targetBook.title).then(res => {
          if (res.data.code === 200) {
            this.$message.success('取消成功')
            this.getBookInfo()
          } else {
            this.$message.error('取消失败')
          }
        })
      } else {
        this.$http.post('/collect/' + username + '/collections/' + this.targetBook.title).then(res => {
          if (res.data.code === 200) {
            this.$message.success('收藏成功')
            this.getBookInfo()
          } else {
            this.$message.error('修改失败')
          }
        })
      }
    }
  }
}
</script>
<style scoped>
.about {
  width: 960px;
  margin: 0 auto;
  padding-top: 20px;
}
</style>
